header nav{
  background-color: #fff;
  Padding: 16px 78px;
  box-shadow: 1px 2px 20px 1px seagreen;
}

.mainheader{
  display: flex;
}

.logo{
  font-size: 32px;
  width: 130px;
  color: #0084ff;
}

.logo p{
  margin: 0;
  font-weight: 500;
  font-family: 'Raleway';
  font-style: italic;
  color: black;
  font-size: .75em;
}
.Explorecategory{
display: flex;
align-self: center;
color: #000;
padding: 0px 72px;
text-align: center;
letter-spacing: 1px;
margin-top: 16px;
}

.bi-chevron-down{
padding-left: 8px;
padding-top: 0;
}

.trendingheader{
line-height: 4;
font-size: 26px;
font-family: Raleway,sans-serif;
color: #000;
font-weight: 600;
margin: 0;
text-align: left;
}

.mainbody{
padding: 2% 16%;
background-color: #deeeff;
}

.filter{
display: flex;
}

.filterpara{
font-weight: 600;
font-size: 14px;
margin-left: 8px;
margin-top: 0px;
}

.btn{
padding: 6px 14px;
width: auto;
color: #0084ff;
border-radius: 15px;
border: 1px solid #0084ff;
margin: 5px;
cursor: pointer;
background: transparent;
}

.active{
color: #fff;
background-color: #0084ff;
}

.categoriescontainer{
margin-bottom: 20px;
font-size: 16px;
padding-top: 16px;
}

.allitems{
display: flex;
flex-direction: row;
flex-wrap: wrap;
padding: 39px;
justify-content: space-between;

}

.itemsections:hover{
  transform: scale(1.1);

}

.images{
width: 100%;
height: 176px;
border-top-left-radius: 5px;
border-top-right-radius: 5px;
}

.containerbody{
padding: 10px 24px;
}

.containerlogo{
color: #0096ff;
font-size: 14px;
font-style: italic;
font-weight: 500;
}

.containerdate{
font-style: normal;
color: rgba(0,0, 0, 7);
font-size: 14px;
font-weight: 300;
}

.containerbodyheader{
font-size: 18px;
text-align: left;
margin: 0;
font-weight: 600;
}

.containerpara{
font-size: 14px;
font-weight: 400;
line-height: 1.43;
color: rgba(0,0, 0, 6);
margin: 4px 0;
opacity: 0.5;
}

.itemssec{
min-height: 25em;
height: auto;
margin-right: 1%;
margin-left: 1%;
margin-bottom: 1em;
background-color: #fff;
max-width: 19.5em;
box-shadow: -9px 6px 6px rgba(0,0,0,0.3);
cursor: pointer;
}


.footerdetails{
background-color: #172146;
padding: 80px 16%;
}

.itemsref{
text-decoration: none;
color: unset;
}

